body {
  margin: 0 auto;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;

}

/************************************ Box CSS ************************************/

.box {
  display: inline-block;       
  border: 1px solid black;
  width: 15px;
  height: 15px;
  margin-left: -1px;
  margin-bottom: -1px;
  }

.box:hover { 
  background: orange;
}

.dead {
  background-color: lightgray;
}

.alive {
  background-color: green;
}

/************************************ Box CSS ************************************/

.grid {
  width: 300px;
  line-height: 0;
  margin: auto;
  box-shadow: 0px 0px 20px white;
  margin-top: 30px;
}

/************************************ Control Bar CSS ************************************/

.control-bar-container {
  width: 100%;
  font-size: 1.6rem;
  opacity: 85%;
  z-index: 999;
}

.control-bar-container-title {
  font-size: 2rem;
  margin-right: 2rem;
}

.control-bar-button {
  text-decoration-color: white;
  margin-right: 2rem;
}

/************************************ Generation Count CSS ************************************/

.gen-count {
  margin: auto;
  text-align: center;
  width: 50%;
  margin-top: 30px;
}

/************************************ About Tag CSS ************************************/

.about-tag {
    text-align: center;
    margin-top: 30px;
}

.go-back-container {
    width: 100%;
    font-size: 1.6rem;
    opacity: 85%;
    z-index: 999;
}

.go-back-title {
  font-size: 2rem;
  margin-left: 50px;
}

.about-text {
  margin-top: 30px;
  margin-left: 20px;
  margin-right: 20px;
}